var cameraPositions = [] as any;
cameraPositions = [0, -.3, -.6, -.9]

function ScrollingCamera(props: any) {
  const ref = useRef<THREE.Camera>(null);
  const { isScrollingUp, isScrollingDown } = useScrollDirection()
  let position = 0;
  let start = 0;
  let end = 0;
  let progress = 0;
  useFrame(() => {

    start = ref!.current!.position.y;
    progress += 0.001;
    if (progress > 1) {
      progress = 0;
    }
    if (isScrollingUp) {
      if (position === 0) {

        position = 3;
      }
      else {
        position--;
      }

    }
    if (isScrollingDown) {
      if (position === 3) {
        position = 0;
      }
      else {
        position++;
      }

    }
    end = cameraPositions[position];
    ref!.current!.position.y = MathUtils.lerp(start, end, progress);
  })





  return (



    <PerspectiveCamera ref={ref} makeDefault fov={60} position={[0, 0, 20]} rotation={[0, 0, 0]} />



  )
}

function Plane({ props }: any) {
  const ref = useRef();
  const [hover, setHover] = useState(false)

  return (
    <mesh ref={ref} receiveShadow rotation={[0, 0, 0]} position={[0, 0, -5]}
      onPointerOver={(e) => setHover(true)}
      onPointerOut={(e) => setHover(false)}>

      <planeBufferGeometry attach="geometry" args={[150, 150]} />
      <meshStandardMaterial attach="material" color='#000000' />

    </mesh>
  )
}
function PhyBox(props: any) {
  const [ref, api] = useBox(() => ({ args: [1, 1, 1], mass: 0.2, ...props }));

  return (
    <Box
      args={[1, 1, 1]}
      ref={ref}

    >
      <meshNormalMaterial />
    </Box>
  );
}
function PhyPlane({ color, ...props }: any) {
  const [ref] = usePlane(() => ({ ...props }));

  return (
    <Plane args={[1000, 1000]} ref={ref} rotation={[0.2, 0.2, 0.2]}>
      <meshStandardMaterial color={color} />
    </Plane>
  );
}

